import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { Link } from 'react-router-dom';
import { PageLayout } from '../components/layout';
import { Card, CardContent, CardHeader } from '../components/ui/card';
import { Button } from '../components/ui/button';
import { Badge } from '../components/ui/badge';
import { Input } from '../components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '../components/ui/select';
import { Switch } from '../components/ui/switch';
import { useToast } from '../components/ui/toast';
import { useAuth } from '../contexts/AuthContext';
import apiService from '../services/api';
import config from '../config/config';
import { BookOpen, Clock, Layers, Star } from 'lucide-react';

const DEFAULT_PAGINATION = {
  currentPage: 1,
  perPage: 12,
  hasNextPage: false,
  hasPrevPage: false,
};

const CoursesPage = () => {
  const { user } = useAuth();
  const { addToast } = useToast();

  const ALL_CATEGORIES_VALUE = 'all';

  const [courses, setCourses] = useState([]);
  const [pagination, setPagination] = useState(DEFAULT_PAGINATION);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [searchTerm, setSearchTerm] = useState('');
  const [category, setCategory] = useState('');
  const [onlyFeatured, setOnlyFeatured] = useState(false);

  const fetchCourses = useCallback(
    async (page = 1) => {
      setLoading(true);
      setError('');
      try {
        const params = {
          page,
          limit: DEFAULT_PAGINATION.perPage,
        };

        if (searchTerm) {
          params.search = searchTerm;
        }
        if (category) {
          params.category = category;
        }
        if (onlyFeatured) {
          params.featured = '1';
        }

        const response = await apiService.listCourses(params);
        if (response?.success) {
          setCourses(response.courses || []);
          setPagination({
            currentPage: response.pagination?.currentPage || page,
            perPage: response.pagination?.perPage || DEFAULT_PAGINATION.perPage,
            hasNextPage: Boolean(response.pagination?.hasNextPage),
            hasPrevPage: Boolean(response.pagination?.hasPrevPage),
          });
        } else {
          setError(response?.message || 'Não foi possível carregar os cursos.');
          setCourses([]);
        }
      } catch (err) {
        console.error('Erro ao carregar cursos:', err);
        setError(err?.message || 'Erro ao carregar cursos.');
        setCourses([]);
        if (err?.status === 401) {
          addToast('Faça login para acessar todos os recursos dos cursos.', 'error');
        }
      } finally {
        setLoading(false);
      }
    },
    [searchTerm, category, onlyFeatured, addToast]
  );

  useEffect(() => {
    fetchCourses(1);
  }, [fetchCourses]);

  const categories = useMemo(() => {
    const values = new Set();
    courses.forEach((course) => {
      if (course.categoria) {
        values.add(course.categoria);
      }
    });
    return Array.from(values).sort();
  }, [courses]);

  const handleClearFilters = () => {
    setSearchTerm('');
  setCategory('');
    setOnlyFeatured(false);
    fetchCourses(1);
  };

  const goToPage = (direction) => {
    if (direction === 'prev' && pagination.hasPrevPage) {
      fetchCourses(Math.max(1, pagination.currentPage - 1));
    } else if (direction === 'next' && pagination.hasNextPage) {
      fetchCourses(pagination.currentPage + 1);
    }
  };

  const renderCourseCard = (course) => {
    const enrollment = course.enrollment;
    const progress = enrollment?.progresso ? Math.round(enrollment.progresso) : 0;
    const imageUrl = course.imagem_capa ? config.getPublicUrl(course.imagem_capa) : null;

    return (
      <Card key={course.id} className="h-full flex flex-col overflow-hidden border border-gray-100 shadow-sm">
        {imageUrl && (
          <div className="h-44 w-full bg-gray-100 overflow-hidden">
            <img src={imageUrl} alt={course.titulo} className="w-full h-full object-cover" />
          </div>
        )}
        <CardHeader className="space-y-3 flex-0">
          <div className="flex items-center justify-between">
            <Badge variant="secondary" className="text-xs">
              {course.categoria || 'Geral'}
            </Badge>
            {course.destaque && (
              <span className="inline-flex items-center gap-1 text-xs font-semibold text-amber-600">
                <Star className="h-3.5 w-3.5" /> Destaque
              </span>
            )}
          </div>
          <h3 className="text-lg font-semibold text-gray-900 leading-snug line-clamp-2">
            {course.titulo}
          </h3>
          <p className="text-sm text-gray-600 line-clamp-3">
            {course.descricao || 'Aprenda com especialistas e avance em sua jornada empreendedora.'}
          </p>
        </CardHeader>
        <CardContent className="flex-1 flex flex-col justify-between space-y-4">
          <div className="space-y-3 text-sm text-gray-600">
            <div className="flex items-center gap-2">
              <BookOpen className="h-4 w-4 text-coral" />
              <span>Nível: {course.nivel || 'Todos'}</span>
            </div>
            {course.duracao_estimado && (
              <div className="flex items-center gap-2">
                <Clock className="h-4 w-4 text-emerald-500" />
                <span>{course.duracao_estimado} min de conteúdo</span>
              </div>
            )}
            <div className="flex items-center gap-2">
              <Layers className="h-4 w-4 text-indigo-500" />
              <span>
                {course.modules?.length || 0} módulo{(course.modules?.length || 0) === 1 ? '' : 's'}
              </span>
            </div>
          </div>

          {enrollment && (
            <div className="space-y-2">
              <div className="flex items-center justify-between text-xs text-gray-500 uppercase tracking-wide">
                <span>Status: {enrollment.status}</span>
                <span>{progress}% concluído</span>
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2">
                <div
                  className="bg-coral h-2 rounded-full transition-all"
                  style={{ width: `${Math.min(100, Math.max(0, progress))}%` }}
                />
              </div>
            </div>
          )}

          <div className="pt-2">
            <Button asChild className="w-full bg-coral hover:bg-coral/90">
              <Link to={`/cursos/${course.slug || course.id}`} state={{ course }}>
                {enrollment ? 'Continuar curso' : user ? 'Ver detalhes' : 'Saiba mais'}
              </Link>
            </Button>
          </div>
        </CardContent>
      </Card>
    );
  };

  return (
    <PageLayout
      title="Trilhas e Cursos EmpowerUp"
      subtitle="Acelere seu crescimento com conteúdos exclusivos, organizado por especialistas do ecossistema."
      breadcrumbs={[
        { label: 'Início', href: '/' },
        { label: 'Cursos' },
      ]}
    >
      <section className="space-y-6">
        <div className="grid gap-4 md:grid-cols-[2fr,1fr,auto] md:items-end">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">Busca por palavra-chave</label>
            <div className="flex gap-2">
              <Input
                placeholder="Ex: marketing digital, finanças, liderança..."
                value={searchTerm}
                onChange={(event) => setSearchTerm(event.target.value)}
                onKeyDown={(event) => {
                  if (event.key === 'Enter') {
                    fetchCourses(1);
                  }
                }}
              />
              <Button type="button" onClick={() => fetchCourses(1)} disabled={loading}>
                Buscar
              </Button>
            </div>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">Categoria</label>
            <Select
              value={category || ALL_CATEGORIES_VALUE}
              onValueChange={(value) => setCategory(value === ALL_CATEGORIES_VALUE ? '' : value)}
            >
              <SelectTrigger>
                <SelectValue placeholder="Todas as categorias" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value={ALL_CATEGORIES_VALUE}>Todas</SelectItem>
                {categories.map((item) => (
                  <SelectItem key={item} value={item}>
                    {item}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          <div className="flex flex-col gap-2">
            <span className="text-sm font-medium text-gray-700">Destaques</span>
            <div className="flex items-center gap-2 text-sm text-gray-600">
              <Switch checked={onlyFeatured} onCheckedChange={setOnlyFeatured} />
              <span>Mostrar apenas cursos em destaque</span>
            </div>
            <Button variant="ghost" className="justify-start text-xs text-gray-500" onClick={handleClearFilters}>
              Limpar filtros
            </Button>
          </div>
        </div>

        {error && (
          <div className="rounded-lg border border-red-200 bg-red-50 p-4 text-sm text-red-700">
            {error}
          </div>
        )}

        {loading ? (
          <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
            {Array.from({ length: 6 }).map((_, index) => (
              <div key={index} className="animate-pulse space-y-4 rounded-lg border border-gray-200 bg-white p-6">
                <div className="h-40 w-full rounded-md bg-gray-200" />
                <div className="h-4 w-1/2 rounded bg-gray-200" />
                <div className="h-3 w-full rounded bg-gray-200" />
                <div className="h-3 w-5/6 rounded bg-gray-200" />
                <div className="h-10 w-full rounded bg-gray-200" />
              </div>
            ))}
          </div>
        ) : courses.length > 0 ? (
          <>
            <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
              {courses.map(renderCourseCard)}
            </div>

            <div className="flex items-center justify-between pt-4 text-sm text-gray-600">
              <span>
                Página {pagination.currentPage}
              </span>
              <div className="flex gap-2">
                <Button
                  variant="outline"
                  size="sm"
                  onClick={() => goToPage('prev')}
                  disabled={!pagination.hasPrevPage || loading}
                >
                  Anterior
                </Button>
                <Button
                  variant="outline"
                  size="sm"
                  onClick={() => goToPage('next')}
                  disabled={!pagination.hasNextPage || loading}
                >
                  Próxima
                </Button>
              </div>
            </div>
          </>
        ) : (
          <div className="rounded-lg border border-dashed border-gray-300 bg-white p-12 text-center">
            <BookOpen className="mx-auto h-12 w-12 text-gray-300" />
            <h3 className="mt-4 text-lg font-semibold text-gray-900">Nenhum curso encontrado</h3>
            <p className="mt-2 text-gray-600">
              Ajuste os filtros de busca ou volte mais tarde para descobrir novos conteúdos.
            </p>
            <Button className="mt-4" onClick={handleClearFilters}>
              Ver todos os cursos
            </Button>
          </div>
        )}
      </section>
    </PageLayout>
  );
};

export default CoursesPage;
